.Task {
  display: -ms-flexbox;
  display: flex;
  background: lightblue;
  background-color: #67B8DE;
  width: 100%;
  margin: 0 0 10px 0;
  padding: 5px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s; }

.Task__toggle {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: start;
      align-self: flex-start;
  width: 24px;
  height: 24px;
  margin: 0 5px 0 0;
  border: 2px solid black;
  background-color: transparent;
  font-size: 0;
  cursor: pointer;
  outline: none; }

.Task__text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0;
  padding: 0; }

.Task__del {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: end;
      align-self: flex-end;
  width: 24px;
  height: 24px;
  border: none;
  background-color: transparent;
  font-size: 0;
  background-image: url("./del.svg");
  background-position: center center;
  background-repeat: none;
  outline: none;
  cursor: pointer; }

.Task--completed {
  opacity: 0.3; }
  .Task--completed .Task__toggle {
    background-image: url("./done.svg");
    background-position: center center;
    background-repeat: none; }
  .Task--completed .Task__text {
    text-decoration: line-through; }



