.Note {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin: 0 0 10px 0;
  padding: 5px;
  background: #B4DCED;
  border-bottom: 2px solid black; }

.Note__text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0;
  padding: 0;
  white-space: pre-wrap; }

.Note__del {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: end;
      align-self: flex-end;
  width: 24px;
  height: 24px;
  border: none;
  background-color: transparent;
  font-size: 0;
  background-image: url("./del.svg");
  background-position: center center;
  background-repeat: none;
  outline: none;
  cursor: pointer; }



